.array-container {
  margin: auto;
  position: relative;
  /*bottom: 15%;*/
  width: 96%;
  height: 85%;
  top: 0px;
  padding: 0 5px;
}

.array-bar {
  width: 0.4%;
  background-color: blue;
  display: inline-block;
  margin: 0 0.2vw;
  vertical-align: top;
  font-size: 50%;
}

#new-array-button {
  border: none;
  font-size: 2.5vh;
  width: 85%;
  height: 60%;
  color: #f5f6fa;
  align-self: center;
  background-color: #40739e;
  border-radius: 5px;
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.button-container {
  position: relative;
  width: 15%;
  margin: 0px;
  float: left;
  height: 100%;
  /*border: 5px solid coral;*/
}

.algorithms-button-container {
  position: relative;
  width: 59%;
  margin: 0 0 0 1%;
  float: left;
  height: 100%;
  /*border: 5px solid coral;*/
}

.sorting-buttons {
  border: none;
  float: left;
  margin: 0.5% 0% 0 0.5%;
  font-size: 2.5vw;
  width: 32.5%;
  height: 45.2%;
  color: #f5f6fa;
  background-color: #40739e;
  /*border-radius: 5px;*/
  /*margin: auto;*/
}

.separator {
  width: 0%;
  float: left;
  height: 80%;
  background-color: #192a56;
}

.adjusting-bar-container {
  float: left;
  width: 25%;
  height: 100%;
  background-color: #273c75;
  /*border: 5px solid coral;*/
}

#adjusting-bar {
  width: 85%; /* Full-width */
  height: 30%; /* Specified height */
  margin-top: 1.5vh;
  background: #d3d3d3; /* Grey background */
  /*-webkit-transition: 0.2s; /* 0.2 seconds transition on hover */
  transition: opacity 0.2s;
}
#interface {
  position: fixed;
  height: 20%;
  border-top: 5px solid #192a56;
  bottom: 0px;
  width: 100%;
  background-color: #273c75;
}

.text {
  margin-top: 4%;
  font-size: 2vh;
  color: #d3d3d3;
  font-style: italic;
}

.array-bar-text {
  padding-top: 2px;
  padding-bottom: 2px;
  font-size: 1.7vw;
  color: #f5f6fa;
}

.time-complexity {
  font-size: 30%;
}
